import React , { useState } from "react";
import { useWorkoutsContext } from "../hooks/useWorkoutContext";

const WorkoutFrom = ()=>{
    const {dispatch} = useWorkoutsContext();
    const [title , setTitles] = useState('');
    const [load , setLoad] = useState('');
    const [reps , SetReps] = useState('');
    const [error , SetError] = useState(null);
    const [emptyFields , setEmptyFields] = useState([])
   
    const handleSubmit = async (e) =>{
        e.preventDefault();
        const workout = {title , load , reps}
        const response = await fetch('/api/workouts',{
            method : 'POST',
            body : JSON.stringify(workout),
            headers:{
                'Content-Type' : 'application/json'
            }
        });
        const json = await response.json()
        if(!response.ok){
            SetError(json.err);
            setEmptyFields(json.emptyFields)
        }
        if(response.ok){
            setTitles('')
            setLoad('')
            SetReps('')
            SetError(null)
            setEmptyFields([])
            dispatch({type : 'CREATE_WORKOUT' ,payload : json})
            console.log('new workout added',json); 

        }

    }

    return (
        <form className="create" onSubmit={handleSubmit} >
            <h3>Add a new Workout</h3>

            <label>Excersize Title</label>
            <input type="text" onChange={(e)=>{setTitles(e.target.value)}} value={title} className={emptyFields.includes('title') ? 'err' : '' }/>
            <label>Load (in kg.)</label>
            <input type="number" onChange={(e)=>{setLoad(e.target.value)}} value={load} className={emptyFields.includes('load') ? 'err' : '' }/>
            <label>Reps </label>
            <input type="number" onChange={(e)=>{SetReps(e.target.value)}} value={reps} className={emptyFields.includes('reps') ? 'err' : '' }/>
        
            <button >Add workout</button>
            {error && <div className="error">{error}</div>}
        </form>
    );
    
}

export default WorkoutFrom;